
#app {
  width: 100%;
  height: 100%;
  font-family: '\5FAE\8F6F\96C5\9ED1', '\9ED1\4F53', 'Arial', 'Verdana', '\5B8B\4F53  b8b\4F53', 'Lato', "Helvetica Neue", 'Helvetica', 'Arial', 'sans-serif';
  background: #efefef;
  overflow: auto;
}
/*css 初始化 */
html,
body,
ul,
li,
ol,
dl,
dd,
dt,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
legend,
div,
img {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
fieldset,
img,
input,
button {
    border: none;
    padding: 0;
    margin: 0;
    /*在低版本的ie浏览器里面不支持*/
    outline-style: none;
}
ul,
ol {
    list-style: none;
}
input {
    padding-top: 0;
    padding-bottom: 0;
    font-family: "SimSun", "\5B8B\4F53";
}
select,
input {
    vertical-align: middle;
}
select,
input,
textarea {
    font-size: 12px;
    margin: 0;
}
/*禁止手动修改大小*/
textarea {
    resize: none;
}
/*去掉图片底部默认空白缝隙*/
img {
    border: 0;
    vertical-align: middle;
}
/*合并外连线*/
table {
    /*表格的单元格边框默认是不会重合的，这个属性可以让表格的边框重合在一起，看起来更加细一些*/
    border-collapse: collapse;
}
html{
    width: 100%;
    height: 100%;
}
body {
    font: 14px/150% Arial, Verdana, "\5B8B\4F53"; /*unitedCode的写法，宋体的写法*/
    color: #1a1a1a;
    height: 100%;
}
/*清除浮动*/
.clearfix:before, .clearfix:after {
    content: "";
    display: table;
}
.clearfix:after {
    clear: both;
}
.clearfix {
    *zoom: 1;
}
a {
    color: #666;
    text-decoration: none;
}
a:hover {
    color: #C81623;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    text-decoration: none;
    font-weight: normal;
    font-size: 100%;
}
s,
i,
em {
    font-style: normal;
    text-decoration: none;
}
/*公共类*/
.w {
    /*版心*/
    width: 1190px;
    margin: 0 auto;
}
.fl {
    float: left;
}
.fr {
    float: right;
}
.al {
    text-align: left;
}
.ac {
    text-align: center;
}
.ar {
    text-align: right;
}
.pr {
    position: relative;
}
.pa {
    position: absolute;
}
.hide {
    display: none;
}
.col-red {
    color: #F10215 !important;
}
/*css初始化完成*/

